"use client";

import { useState, useEffect } from "react";
import { Users, Calendar, Home, User } from "lucide-react"; // Importing icons
import GroupSelector from "../components/PlanifierSoutenance/GroupSelector";
import DateTimePicker from "../components/PlanifierSoutenance/DateTimePicker";
import SubmitButton from "../components/PlanifierSoutenance/SubmitButton";
import JurySelector from "../components/PlanifierSoutenance/JurySelector";
import RoomSelector from "../components/PlanifierSoutenance/RoomSelector";

interface Group {
  idGroupe: string;
  nomGroupe: string;
  nbEtudiants: number;
}

interface Jury {
  idJury: number;
  nom: string;
  valideDeliberation: number;
}

interface Room {
  id: string;
  name: string;
}

interface Soutenance {
  date: string;
  time: string;
  location: string;
  jury: string;
  group: string;
}

const PlanifierSoutenance = () => {
  const [newSoutenance, setNewSoutenance] = useState<Soutenance>({
    date: "",
    time: "",
    location: "",
    jury: "",
    group: "",
  });
  const [groups, setGroups] = useState<Group[]>([]);
  const [jurys, setJurys] = useState<Jury[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const rooms: Room[] = [
    { id: "1", name: "Salle 12" },
    { id: "2", name: "Salle 42" },
    { id: "3", name: "Salle 21" },
  ];

  useEffect(() => {
    const fetchData = async () => {
      try {
        const [groupsResponse, jurysResponse] = await Promise.all([
          fetch("http://localhost:5000/api/groups"),
          fetch("http://localhost:5000/api/jurys"),
        ]);
        const groupsData = await groupsResponse.json();
        const jurysData = await jurysResponse.json();
        setGroups(groupsData);
        setJurys(jurysData);
      } catch (error) {
        console.error("Error fetching data:", error);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, []);

  const addSoutenance = () => {
    if (
      newSoutenance.date &&
      newSoutenance.time &&
      newSoutenance.location &&
      newSoutenance.jury &&
      newSoutenance.group
    ) {
      alert("Soutenance ajoutée avec succès !");
      console.log("Nouvelle soutenance:", newSoutenance);
      setNewSoutenance({
        date: "",
        time: "",
        location: "",
        jury: "",
        group: "",
      });
    } else {
      alert("Veuillez remplir tous les champs !");
    }
  };

  return (
    <div className="p-8 bg-gray-100 min-h-screen">
      <h1 className="text-5xl font-semibold text-gray-800 mb-12 text-center">
        📅 Planification des Soutenances
      </h1>
      <div className="max-w-7xl mx-auto bg-white p-10 rounded-lg shadow-xl">
        <h2 className="text-4xl font-bold text-gray-700 mb-8 text-center">
          📝 Définir une nouvelle soutenance
        </h2>
        {/* Form Sections */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
          {/* Group Section */}
          <div className="bg-gray-50 p-6 rounded-lg shadow-md flex flex-col items-center justify-center">
            <h3 className="text-2xl font-semibold text-gray-800 mb-4 flex items-center justify-center border-b-2 border-gray-300 pb-2 w-full text-center">
              <Users className="mr-3 text-gray-500" />Groupes
            </h3>
            <div className="w-full">
              <GroupSelector
                groups={groups}
                selectedGroup={newSoutenance.group}
                onSelectGroup={(groupId) =>
                  setNewSoutenance({ ...newSoutenance, group: groupId })
                }
              />
            </div>
          </div>

          {/* Date & Time Section */}
          <div className="bg-gray-50 p-6 rounded-lg shadow-md flex flex-col items-center justify-center">
            <h3 className="text-2xl font-semibold text-gray-800 mb-4 flex items-center justify-center border-b-2 border-gray-300 pb-2 w-full text-center">
              <Calendar className="mr-3 text-gray-500" /> la date et l'heure
            </h3>
            <DateTimePicker
              date={newSoutenance.date}
              time={newSoutenance.time}
              onDateChange={(date) => setNewSoutenance({ ...newSoutenance, date })}
              onTimeChange={(time: any) =>
                setNewSoutenance({ ...newSoutenance, time })
              }
            />
          </div>
        </div>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 mb-8">
          {/* Room Section */}
          <div className="bg-gray-50 p-6 rounded-lg shadow-md flex flex-col items-center justify-center">
            <h3 className="text-2xl font-semibold text-gray-800 mb-4 flex items-center justify-center border-b-2 border-gray-300 pb-2 w-full text-center">
              <Home className="mr-3 text-gray-500" /> les salles
            </h3>
            <RoomSelector
              rooms={rooms}
              selectedRoom={newSoutenance.location}
              onSelectRoom={(location) =>
                setNewSoutenance({ ...newSoutenance, location })
              }
            />
          </div>

          {/* Jury Section */}
          <div className="bg-gray-50 p-6 rounded-lg shadow-md flex flex-col items-center justify-center">
            <h3 className="text-2xl font-semibold text-gray-800 mb-4 flex items-center justify-center border-b-2 border-gray-300 pb-2 w-full text-center">
              <User className="mr-3 text-gray-500" /> les jurys
            </h3>
            <JurySelector
              selectedJury={newSoutenance.jury}
              onSelectJury={(jury) => setNewSoutenance({ ...newSoutenance, jury })}
              jurys={jurys}
              loading={loading}
            />
          </div>
        </div>

        {/* Submit Button */}
        <div className="flex justify-center">
          <SubmitButton onClick={addSoutenance} />
        </div>
      </div>
    </div>
  );
};

export default PlanifierSoutenance;
